<template>
  <div class="temperament">
    <div v-for="(i,index) in 40" :key="index"
         :class="`hight ${musicPlay?'anima':''} ${progress>index?'progress':''}`">
    </div>
  </div>
</template>

<script>
export default {
  name: 'Temperament',
  data() {
    return {
      progress: Math.floor(this.musicTime / this.music),
      timer: Function,
    };
  },
  props: {
    music: Number,
    musicTime: Number,
  },
  computed: {
    musicPlay() {
      return this.$store.state.musicStatus;
    },
  },
  mounted() {
    this.timer = setInterval(() => {
      this.progress = Math.floor((this.musicTime / this.music) * 40);
    }, 1000);
  },
  destroyed() {
    clearInterval(this.timer);
  },
};
</script>

<style lang="scss" scoped>
  .temperament {
    height: 100%;
    display: flex;
    align-items: center;
    margin-left: 50px;
    overflow: hidden;

    .hight {
      width: 5px;
      height: 1px;
      margin-left: 2px;
      background: #a7a7a7;

    }

    .anima {
      @for $i from 1 through 70 {
        &:nth-child(#{$i}) {
          animation: move linear 0.5s ((random(1000)-500)/1000+s) infinite alternate;
        }
      }
    }

    .progress {
      background: #123a70;
    }
  }

  /* 声明动画 */
  @keyframes move {
    from {
      height: 0;
    }
    to {
      height: 100%;
    }
  }
</style>
