<template>
  <div class="music">
    <header class="title">
      <router-link to="/">
        <van-icon size="0.25rem" name="arrow-left"/>
      </router-link>
      <h1>Now listening</h1>
      <van-icon size="0.25rem" name="more-o"/>
    </header>
    <main class="main">
      <!--球动画-->
      <div class="shadowBox">
        <van-image class="image"
                   round
                   width="2rem"
                   height="2rem"
                   :src="musicList[musicIndex].musicImg"
        />
        <div :class="`shadow ${musicPlay?'myAnima1':''}`"></div>
        <div :class="`shadow ${musicPlay?'myAnima2':''}`"></div>
      </div>
      <h1 class="musicName" style="margin-top: 0.3rem">{{musicList[musicIndex].musicName}}</h1>
      <p class="musicSinger" style="margin-top: 0.1rem">{{musicList[musicIndex].musicSinger}}</p>
      <!--波形图-->
      <div class="temperament">
        <Temperament :music="music.duration" :musicTime="musicTime"/>
      </div>
      <!--时间-->
      <h3 class="time"><span>{{musicTime | format}}</span>
        <span>{{music.duration | format}}</span></h3>
      <div class="buttom">
        <span @click="()=>{this.$store.commit('beforeMusic')}"
              class="iconfont icon-shangyishoushangyige"></span>
        <span class="play" @click="()=>{this.$store.commit('playMusic')}">
        <van-icon v-show="!musicPlay" name="play-circle-o"/>
        <van-icon v-show="musicPlay" name="pause-circle-o"/>
        </span>
        <span @click="()=>{this.$store.commit('lastMusic')}"
              class="iconfont icon-xiayigexiayishou"></span>
      </div>
    </main>
  </div>
</template>

<script>
import Temperament from '../Home/Temperament/Temperament.vue';

export default {
  name: 'Music',
  data() {
    return {
      musicTime: this.$store.state.music.currentTime,
      timer: Function,
    };
  },
  components: { Temperament },
  computed: {
    musicPlay() {
      return this.$store.state.musicStatus;
    },
    music() {
      return this.$store.state.music;
    },
    musicList() {
      return this.$store.state.musicList;
    },
    musicIndex() {
      return this.$store.state.musicIndex;
    },
  },
  filters: {
    format(value) {
      if (!value) {
        return '00:00';
      }
      const min = Math.floor(value / 60);
      const s = Math.floor(value % 60);
      let time = '';
      if (min < 10) {
        time += `0${min}:`;
      } else {
        time += `${min}:`;
      }
      if (s < 10) {
        time += `0${s}`;
      } else {
        time += `${s}`;
      }
      return time;
    },
  },
  mounted() {
    setInterval(() => {
      this.musicTime = this.$store.state.music.currentTime;
    }, 1000);
  },
  destroyed() {
    clearInterval(this.timer);
  },
};
</script>

